.pop-enter {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  -webkit-transform: translateY(50%);
          transform: translateY(50%); }

.pop-enter-active {
  opacity: 1;
  visibility: visible;
  pointer-events: none;
  -webkit-transform: translateY(0);
          transform: translateY(0); }

.pop-enter-done {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  -webkit-transform: translateY(0);
          transform: translateY(0); }

.pop-exit {
  opacity: 1;
  visibility: visible;
  pointer-events: none;
  -webkit-transform: translateY(0);
          transform: translateY(0); }

.pop-exit-active {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%); }

.pop-exit-done {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%); }

.fade-enter {
  opacity: 0;
  visibility: hidden;
  pointer-events: none; }

.fade-enter-active {
  opacity: 1;
  visibility: visible;
  pointer-events: none; }

.fade-enter-done {
  opacity: 1;
  visibility: visible;
  pointer-events: auto; }

.fade-exit {
  opacity: 1;
  visibility: visible;
  pointer-events: none; }

.fade-exit-active {
  opacity: 0;
  visibility: hidden;
  pointer-events: none; }

.fade-exit-done {
  opacity: 0;
  visibility: hidden;
  pointer-events: none; }
