#app-loading .loading {
  -webkit-animation: 1.5s cubic-bezier(0.75, 0, 0.5, 1) infinite loading;
          animation: 1.5s cubic-bezier(0.75, 0, 0.5, 1) infinite loading;
  background: #00a862;
  -webkit-border-radius: 4px;
          border-radius: 4px;
  -webkit-box-shadow: 0 1px 5px rgba(0, 168, 98, .5);
          box-shadow: 0 1px 5px rgba(0, 168, 98, .5);
